"use client";
import { useState } from "react";
import { useRouter } from "next/navigation";
import { supabase } from "@/lib/supabaseClient";

export default function AdminLoginPage() {
  const router = useRouter();
  const [email, setEmail] = useState("admin@admin.com"); // pre-fill optional
  const [password, setPassword] = useState("");
  const [loading, setLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState<string | null>(null);

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setErrorMessage(null);

    const { data, error } = await supabase.auth.signInWithPassword({
      email,
      password,
    });

    setLoading(false);

    if (error) {
      setErrorMessage(error.message);
    } else {
      router.push("/admin"); // redirect to dashboard
    }
  };

  return (
    <div style={{
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center',
      width: '100vw',
      height: '100vh',
      backgroundColor: '#401010',
      color: 'white',
      fontFamily: 'monospace',
      padding: '20px',
    }}>
      <div style={{ border: '4px dashed white', padding: '40px', minWidth: '400px' }}>
        <h1 style={{ fontSize: '36px', marginBottom: '20px' }}>Admin Login</h1>
        <form onSubmit={handleLogin} style={{ display: 'flex', flexDirection: 'column', gap: '15px' }}>
          <input
            type="email"
            placeholder="Email"
            value={email}
            onChange={e => setEmail(e.target.value)}
            style={{ padding: '10px', fontSize: '16px', color: 'black' }}
            required
          />
          <input
            type="password"
            placeholder="Password"
            value={password}
            onChange={e => setPassword(e.target.value)}
            style={{ padding: '10px', fontSize: '16px', color: 'black' }}
            required
          />
          {errorMessage && (
            <div style={{ color: 'red', fontWeight: 'bold' }}>{errorMessage}</div>
          )}
          <button
            type="submit"
            disabled={loading}
            style={{ padding: '10px', fontSize: '18px', cursor: 'pointer' }}
          >
            {loading ? "Signing in..." : "Login"}
          </button>
        </form>
      </div>
    </div>
  );
}
